.resisterform{
    display: flex;
    justify-content: center;
    align-items: center;
}
.Resisterbtn{
    text-decoration: none;
}
.maindiv{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    /* border: 2px solid green; */
    /* flex-direction: column; */
}

.loginpage{
    border: 2px solid red;
    align-items: center;
    height:auto ;
    width: 40vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 10px;
}
.heading{
    text-align: center;
    color: blue;
    text-transform: uppercase;

}
.loginpage input{
    padding: 10px;
    width: 30rem;

}
.submitbtn{
    margin-top: 10px;
    /* border: 2px solid green; */
    padding: 10px;
    font-size: 25px;
    text-transform: capitalize;
    background-color: black;
    color: white;
    border-radius: 10px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}
.submitbtn:hover{
    background-color:rgb(89, 74, 64) ;
}
.Resisterbtn{
    margin-top: 10px;
    /* border: 2px solid green; */
    padding: 10px;
    font-size: 25px;
    text-transform: capitalize;
    background-color: blue;
    color: black;
    border-radius: 10px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}
.Resisterbtn:hover{
    background-color:rgb(33, 104, 128) ;
}
.loginpage ::placeholder{
    text-transform: capitalize;
}

@media screen and (max-width:1200px) {
   .loginpage input{
    width:auto;
   }
   .loginpage{
    height: auto;
   }
}
.navbar{
    display: flex;
    justify-content: space-between;
    width: 500px;
    margin: auto;
    font-size: 30px;
    text-decoration: none;
    text-transform: capitalize;
    color: black;
}